.role-selection-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
  }
  
  h2 {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .roles {
    display: flex;
    justify-content: space-between;
    width: 60%;
    margin-bottom: 20px;
  }
  
  .role {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px;
    border: 2px solid transparent;
    cursor: pointer;
    transition: border-color 0.3s ease;
  }
  
  .role.selected {
    border-color: #007bff;
  }
  
  .role img {
    width: 80px;
    height: 80px;
    margin-bottom: 10px;
  }
  
  .next-btn {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .next-btn:hover {
    background-color: #0056b3;
  }
  